import request from '@/common/api/request.js'
export default {
    data() {
       return {
            
       }
    },
	onLoad(options) {
		//
		if (options.scene && options.scene !== '' && options.scene !== null) {
			console.log('')
			const scene = decodeURIComponent(options.scene)
			console.log(scene)
			if(!uni.getStorageSync('openid')){
				this.getOpenid(scene)
			}
			
		} else{
			console.log('')
			if(!uni.getStorageSync('openid')){
				this.getOpenid("0")
			}
			
		}
	},
    methods: {
		async getOpenid (sharerPhone){
			//code
			const [err, res] = await uni.login().catch(err => err)
			this.code = res.code
			const url = '/api/Example/getOpenid';
			const method = 'POST'; 
			const data = {
				js_code: this.authCode,
				secret: ""
			};
			try {
				const response = await request('yzy_app', url, method, data);	//{"data":{}, "flag":99, "result":""}
				if(response.code == 99){
					uni.setStorageSync("openid", response.data.openid)
					this.checkUserExist(response.data.openid,sharerPhone)
					
				}
				else {
					
				}
			} catch (error) {
				console.log(error);
			}
		},
		async checkUserExist(openid,sharerPhone){
			const url = '/api/user/checkUserExist';
			const method = 'POST'; 
			const data = {
				openid: openid,
				sharerPhone: sharerPhone,
			};
			try {
				const response = await request('yzy_app', url, method, data);		//{"data":{}, "flag":99, "result":""}
				if(response.code == 99){
				}
				else {
		
				}
			} catch (error) {
				console.log(error);
			}
		},
		
	}
}